body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
  justify-content: center;
  min-height: 100%; 
  min-height: 100vh;

  display: flex;
  align-items: center;
}

.App-link {
  color: #61dafb;
}


.logo {
  height: auto;
  max-width: 3rem;
  width: 2rem;
}

.myPicture {
  height: auto;
  width: 250px;
  max-width: 50%vh;
}

.horizontalMargin {
  margin-left: 1rem;
  margin-right: 1rem;
}
